import React from "react";

import "./styles.scss";

export default function CountryFlag({
  countryCode,
  flagClassName = "",
  wrapperClassName = ""
}: T_CountryFlagProp) {
  return (
    <span className={wrapperClassName}>
      <i
        className={`countryFlagSprite countryFlag-${countryCode.toLowerCase()} ${flagClassName}`}
      ></i>
    </span>
  );
}

type T_CountryFlagProp = {
  countryCode: string;
  wrapperClassName?: string;
  flagClassName?: string;
};
