import React from "react";

import * as CONST_CURRENCY_EX from "constants/currencyEx";
import CountryFlag from "components/CountryFlag";
import { cacheInputCodeSync } from "helpers/API/currencyEx";
import {
  T_CurrencyExActionTypes,
  T_FavoriteWithExRate,
  useCurrencyExReducer
} from "./hooks";
import "./styles.scss";

export default function CurrencyEx() {
  const [state, dispatch] = useCurrencyExReducer();
  const handleInputAmountChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    dispatch([T_CurrencyExActionTypes.setInputAmount, event.target.value]);
  };
  const handleCurrencyItemClick = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    const { currencyCode } = event.currentTarget.dataset;
    if (currencyCode) {
      cacheInputCodeSync(currencyCode);
      dispatch([T_CurrencyExActionTypes.setInputCode, currencyCode]);
    }
  };

  const selectedCurrencyInfo = CONST_CURRENCY_EX.currencyMap[state.inputCode];
  console.log(
    "\n⎣%s⎦ ==> src/pages/Tools/Money/CurrencyEx/index.tsx:35 ‣ ",
    new Date().toISOString(),
    state,
    "\n"
  ); // eslint-disable-line

  return (
    <div className="MoneyEx">
      <div className="gridContainer">
        <div className="inputOutput">
          <CountryFlag
            countryCode={selectedCurrencyInfo.countryCode}
            wrapperClassName="mt-1 pr-2"
          />
          <span className="codeSymbolGroup">
            {state.inputCode} {selectedCurrencyInfo.symbol}
          </span>
          <input
            autoFocus
            placeholder="Type amount..."
            className="inputField"
            type="text"
            onChange={handleInputAmountChange}
            value={state.inputAmount}
          />
        </div>
        {/* <div className="fullList">
          {getCurrencyListMarkup({
            handleCurrencyItemClick
          })}
        </div> */}
        <div className="favList">
          {getCurrencyListMarkup({
            favorites: state.favorites,
            handleCurrencyItemClick,
            currentInputCode: state.inputCode
          })}
        </div>
      </div>
    </div>
  );
}

function getCurrencyListMarkup({
  handleCurrencyItemClick,
  favorites,
  currentInputCode
}: {
  handleCurrencyItemClick: (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => void;
  favorites?: T_FavoriteWithExRate[];
  currentInputCode?: string;
}) {
  const arrSource = favorites || Object.keys(CONST_CURRENCY_EX.currencyMap);

  return (
    <>
      {arrSource.map((currency) => {
        const code = favorites
          ? (currency as T_FavoriteWithExRate).currencyCode
          : (currency as string);

        // Do not render the currency item that is already the input
        if (currentInputCode === code) {
          return null;
        }

        const currencyInfo = CONST_CURRENCY_EX.currencyMap[code];

        return (
          <div
            key={code}
            className="itemContainer group"
            data-currency-code={code}
            onClickCapture={handleCurrencyItemClick}
          >
            <div className="codeFlagGroup group-hover:animate-bounce">
              <CountryFlag
                countryCode={currencyInfo.countryCode}
                wrapperClassName="-mt-1 pr-2"
              />
              {code}
            </div>
            <div className="valueNameGroup">
              {favorites && (
                <span className="exRate">
                  {currencyInfo.symbol || ""}&nbsp;
                  {(currency as T_FavoriteWithExRate).exRate ?? 0}
                </span>
              )}
              <div className="name">
                {(code && currencyInfo?.name) || "..."}
              </div>
            </div>
          </div>
        );
      })}
    </>
  );
}
